<template>
  <div id="app">
    <h2>게시글 상세보기</h2>
    <div class="detailWrap">
      <table class="postDetail">
        <colgroup>
          <col width="25%" />
          <col width="*" />
        </colgroup>
        <tr>
          <th>no</th>
          <td>{{post.id}}</td>
        </tr>
        <tr>
          <th>제목</th>
          <td>{{post.title}}</td>
        </tr>
        <tr>
          <th>작성자</th>
          <td>{{post.writer}}</td>
        </tr>
        <tr>
          <th>날짜</th>
          <td>{{post.created_at}}</td>
        </tr>
        <tr>
          <th>내용</th>
          <td class="content">{{post.content}}</td>
        </tr>
      </table>
    </div>

    <div class="detailBtnWrap">
      <a v-on:click="backList()" class="backBtn btn">목록</a>
      <a v-on:click="editContent()" class="editBtn btn">수정</a>
      <a v-on:click="deleteContent()" class="deleteBtn btn">삭제</a>
    </div>
  </div>
</template>

<script>
// 데이터를 가져오기 위해 api url import
import { API_BASE_URL } from '@/config'
import $ from 'jquery'

export default {
  data () {
    return {
      post: []
    }
  },

  created () {
    // query값 받아와서 api서버의 url로
    var id = this.$route.query.id
    var self = this

    // 파라미터를 url이 아닌 data로 전달
    $.ajax({
      url: API_BASE_URL + '/post',
      method: 'get',
      data: {
        id: id
      },
      dataType: 'json',
      success: function (data) {
        // this가 할당하는게 뭔지 잘 확인하기
        self.post = ({
          id: data.id,
          title: data.title,
          writer: data.writer,
          created_at: data.created_at,
          content: data.content
        })
      },
      error: function (error) {
        console.log(error)
      },
      async: false
    })

    // fetch(API_BASE_URL + '/post/' + id)
    //   .then((response) => {
    //     if (response.ok) {
    //       return response.json()
    //     }
    //     throw new Error('Network response was not ok')
    //   })
    //   .then((json) => {
    //     this.post = ({
    //       id: json.data.id,
    //       title: json.data.title,
    //       writer: json.data.writer,
    //       created_at: json.data.created_at,
    //       content: json.data.content
    //     })
    //   })
  },
  methods: {
    backList () {
      this.$router.push('list')
    },

    editContent () {
      this.$router.push({path: 'new', query: {id: this.post.id}})
    },

    deleteContent () {
      var id = this.$route.query.id

      $.ajax({
        url: API_BASE_URL + '/post/delete',
        method: 'post',
        dataType: 'json',
        data: {
          id: id
        },
        contentType: 'application/x-www-form-urlencoded',
        success: function () {
          console.log('ok')
        },
        error: function (e) {
          console.log(e)
        }
      })

      this.$router.push('list')
    }
  }
}
</script>

<style scoped>
.postDetail {
  border-top: 1px solid #888;
}

.postDetail th, .postDetail td {
  border-bottom: 1px solid #eee;
  padding: 5px 0;
}

.postDetail td {
  padding: 10px 10px;
  box-sizing: border-box;
  text-align: left;
}

.postDetail td.content {
  height: 300px;
  vertical-align: top;
}

.detailBtnWrap {
  text-align: center;
  margin: 20px 0 0 0;
}

.detailBtnWrap a {
  margin: 0 10px;
}

.backBtn {
  background: #34445c;
}

.editBtn {
  background: #43b984;
}

.deleteBtn {
  background: #43b984;
}
</style>



