// 게시판 리스트

<template>
  <div id="app">
    <h2>게시글 리스트</h2>

    <!-- 리스트 테이블 -->
    <div class="listWrap">
      <table class="postList">
        <colgroup>
          <col width="25%" />
          <col width="25%" />
          <col width="25%" />
          <col width="25%" />
        </colgroup>
        <tr>
          <th>no</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <tr v-for="(post, index) in postList" :key="index">
          <td>{{post.id}}</td>
          <!-- 게시글 제목을 클릭하면 상세페이지로 연결되는 링크 -->
          <td><a v-on:click="viewDetail(post.id)">{{post.title}}</a></td>
          <td>{{post.writer}}</td>
          <td>{{post.created_at}}</td>
        </tr>
      </table>
    </div>

    <div class="moveToWrap">
      <a v-on:click="addContent()" class="addBtn btn">등록</a>
    </div>
  </div>
</template>

<script>
import { API_BASE_URL } from '@/config'
import $ from 'jquery'

export default {
  data () {
    return {
      postList: []
    }
  },

  created () {
    var self = this
    $.ajax({
      url: API_BASE_URL + '/post',
      method: 'get',
      dataType: 'json',
      success: function (data) {
        // for문이 안돌아ㅏ data.length가 undefined 왜/??????
        // json형식은 배열이 아니기 때문에 길이 가져오는 방법이 다름!
        console.log(data)
        var len = Object.keys(data).length

        // this가 할당하는게 뭔지 잘 확인하기
        // laravel의 경우 data.data로 접근
        for (var i = 0; i < len; i++) {
          self.postList.push({
            id: data[i].id,
            title: data[i].title,
            writer: data[i].writer,
            created_at: data[i].created_at,
            content: data[i].content
          })
        }
      },
      error: function (error) {
        console.log(error)
      },
      async: false
    })

    // fetch(API_BASE_URL + '/post')
    //   .then((response) => {
    //     if (response.ok) {
    //       return response.json()
    //     }
    //     throw new Error('Network response was not ok')
    //   })
    //   .then((json) => {
    //     for (var i = 0; i < json.data.length; i++) {
    //       this.postList.push({
    //         id: json.data[i].id,
    //         title: json.data[i].title,
    //         writer: json.data[i].writer,
    //         created_at: json.data[i].created_at,
    //         content: json.data[i].content
    //       })
    //     }
    //   })
    //   .catch((error) => {
    //     console.log(error)
    //   })
  },

  methods: {
    addContent () {
      this.$router.push('new')
    },

    viewDetail (id) {
      // post의 제목 클릭하면 post의 id를 받아와서 파라미터로 넘겨줌
      this.$router.push({path: 'detail', query: { id: id }})
    }
  }

}
</script>

<style scoped>
  .postList th {
    border-top: 1px solid #888;
  }

  .postList th, .postList td {
    border-bottom: 1px solid #eee;
    padding: 5px 0;
  }

  .postList td.text_left {
    text-align : left;
  }

  .moveToWrap {
    text-align: right;
    margin: 10px 0 0 0;
  }
</style>



