<template>
  <div id="app">
    <!-- id값에 따라서 제목 변경 -->
    <h2>게시글 {{id ? '수정':'작성'}}</h2>

    <div class="newWrap">
      <form>
        <table class="postNew">
          <colgroup>
            <col width="15%">
            <col width="*">
          </colgroup>
          <tr>
            <th>제목</th>
            <td><input type="text" v-model="title" placeholder="title"></td>
          </tr>
          <!-- 이미 작성한 게시물의 작성자는 변경 불가능하게 함 -->
          <tr v-if="!id">
            <th>작성자</th>
            <td>
              <input type="text" v-model="writer" placeholder="user">
            </td>
          </tr>
          <tr>
            <th>내용</th>
            <td><textarea v-model="content" placeholder="text" /></td>
          </tr>
        </table>
      </form>
    </div>

    <div class="newBtnWrap">
      <a href="javascript:;" v-on:click="backList()" class="backBtn btn">목록</a>
      <a v-if="!id" v-on:click="addContent()" class="addBtn btn">등록</a>
      <a v-else v-on:click="editContent()" class="addBtn btn">수정</a>
    </div>
  </div>

</template>

<script>
import { API_BASE_URL } from '@/config'
import $ from 'jquery'

export default {
  data () {
    return {
      id: this.$route.query.id,
      title: '',
      writer: '',
      content: ''
    }
  },

  methods: {
    backList () {
      this.$router.push('list')
    },
    // 추가버튼 이벤트
    addContent () {
      if (!this.title) { // 제목이 없다면
        alert('제목을 입력해 주세요.')
      }

      this.form = { // backend로 전송될 POST데이터
        title: this.title,
        writer: this.writer,
        content: this.content
      }

      // ajax통신
      $.ajax({
        url: API_BASE_URL + '/post',
        data: {
          data: JSON.stringify(this.form)
        },
        method: 'post',
        contentType: 'application/x-www-form-urlencoded',
        success: function () {
          console.log('ok')
        },
        error: function (e) {
          console.log(e)
        }
      })

      // 새로고침을 해야 적용이 되는 문제 해결해야 함
      this.$router.push('list')
    },
    // 수정버튼 이벤트
    editContent () {
      if (!this.title) { // 제목이 없다면
        alert('제목을 입력해 주세요.')
        this.$refs.subject.focus()
      }

      this.form = { // backend로 전송될 POST데이터
        title: this.title,
        content: this.content
      }

      // ajax통신
      const self = this
      $.ajax({
        url: API_BASE_URL + '/post',
        data: {
          id: self.id,
          data: JSON.stringify(this.form)
        },
        method: 'post',
        contentType: 'application/x-www-form-urlencoded',
        success: function () {
          console.log('ok')
        },
        error: function (e) {
          console.log(e)
        }
      })

      // 새로고침을 해야 적용이 되는 문제 해결해야 함
      this.$router.push('list')
    }
  }

}
</script>

<style scoped>
  .postNew {
    border-top: 1px solid #888;
  }

  .postNew th, .postNew td {
    border-bottom: 1px solid #eee;
    padding: 5px 0;
  }

  .postNew td {
    padding: 10px 10px;
    box-sizing: border-box;
  }

  .postNew td input {
    width: 100%;
    min-height: 30px;
    box-sizing: border-box;
    padding: 0 10px;
  }

  .postNew td textarea {
    width: 100%;
    min-height: 300px;
    padding: 10px;
    box-sizing: border-box;
  }

  .newBtnWrap {
    text-align: center;
    margin: 20px 0 0 0;
  }

  .newBtnWrap a {
    margin: 0 10px;
  }

  .addBtn {
    background: #43b984;
  }

  .backBtn {
    background: #34445c;
  }
</style>



